
.main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 5vw;
}
.image-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 5vw;
}

.project-image {
    border-radius: 2rem;
    box-shadow: 0 0.5rem 0.5rem rgb(40, 40, 40);
    cursor: pointer;
    transition: 0.25s;
}

 .tooltip {
    position: relative;
    padding: 0.5rem 1rem;
    background-color: white;
    border-radius: 0.5rem;
    font-size: 1.25rem;
    width: fit-content;
    box-shadow: 0 0.5rem 0.5rem grey;
    opacity: 0;
    left: 0;
    bottom: 0;
}

.dark .tooltip {
    background-color: rgb(33, 33, 33);
}

.tooltip-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.tooltip-container:hover .tooltip  {
    opacity: 1;
    transition: 0.5s;
}

.rectangle-image {
    height: 22rem;
}

.portrait-image {
    width: 22rem;
    margin-top: 0;
}

.square-image {
    width: 20rem;
    margin-top: 0;
}